export default {
	data() {
		return {
			title: ''
		}
	},
	onLoad:function(e){
		//console.log(e);
		var newsid=e.newsid;
		console.log(newsid);
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news/36kr/'+newsid,
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				this.title = res.data.title;
			},
			fail: () => {},
			complete: () => {}
		});
		
	},
	
}
